import React from "react";
import ProblemSelector from "../selectProblem";
import {Card} from "antd";
import InfiniteCarousel from 'react-leaf-carousel';
import axios from "axios";
import {DATA_DISPLAY_URL} from "../../components/api";

class GalleryIM extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      images: [],
      uniqueId: props.uniqueId
    }
  }
  componentDidMount(){
    var self = this;

    axios.get(DATA_DISPLAY_URL+"?project_id="+self.state.uniqueId, {
    temp: 'temp'
    })
    .then(function (response) {
      console.log(response);
       if(response.status===200){
         console.log(response);
         let images_ = response.data;
         let imag = images_.map(image => {
                    return (
                      <Card key={image.src} title={image.caption} className="gx-card" style={{textAlign:'center'}}>
                       <img src={image.src} alt={image.caption} />
                     </Card>
                  );
                 });
        self.setState({images: imag});
       }
    })
    .catch(function (error) {
      console.log(error);
    });
  }
  render() {
    return (
      <div>
      {/*<ProblemSelector/>*/}
      <InfiniteCarousel
   breakpoints={[
     {
       breakpoint: 500,
       settings: {
         slidesToShow: 2,
         slidesToScroll: 2,
       },
     },
     {
       breakpoint: 768,
       settings: {
         slidesToShow: 3,
         slidesToScroll: 3,
       },
     },
   ]}
   dots={false}
   showSides={false}
   sidesOpacity={.5}
   sideSize={.1}
   slidesToScroll={2}
   slidesToShow={3}
   scrollOnDevice={true}
   lazyLoad={true}
   swipe={true}
   responsive={true}
   arrows={true}
   autoCycle={true}
 >
 {this.state.images}
 </InfiniteCarousel>
      </div>
    );
  }

}

export default GalleryIM;
